.plans_container{
    width: 100%;
    margin: 2em 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.plans_main_container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1em;
}

.information_container{
    width: 50vw;
    padding: 1em;
    background-color: bisque;
    border-radius: 15px;
    box-shadow: 10px 10px 20px gray;
    margin-bottom: 2em;
}

.line{
    height: 1px;
    width: 100%;
    border: 1px solid black;
}

h3{
    text-align: center;
    font-size: 38px;
    padding: 0 1em;
}

h4{
    font-size: 28px;
    padding: 0 1em;
}

h5{
    font-size: 20px;
    font-weight: 400;
    padding: 0 1em;
}

.information_container h3{
    margin: 1em 0;
}

.information_container ul{
    margin: 1em 0;
}

@media screen and (max-width: 720px) {
    .plans_container{
        flex-direction: column;
        justify-content: center;
    }

    .information_container{
        width: 90vw;
        box-shadow: 5px 5px 10px gray;
    }
}