.footer-container{
    margin-top: auto;
    width: 100vw;
    padding: 25px 0;
    background-color: black;
}
.contact-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.social_links_container{
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.hr_line{
    width: 25%;
    height: 1px;
    border: 1px solid white;
}
.social_links_container ul{
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
}
.social_links_container ul li{
    margin: 0 50px;
    background-color: black;
} 
.social_links_container ul li img{
    background-color: white;
    border: 2px solid white;
    border-radius: 50%;
}
.copyright_container{
    margin-top: 25px;
    color: white;
    font-size: 10px;
}

@media screen and (max-width: 720px) {
    .hr_line{
        width: 20%;
    }
    .social_links_container ul{
        padding: 0;
    }
    .social_links_container ul li{
        margin: 0 10px;
    } 
}