.pricing_cards_main_container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.pricing_card_container{
    width: 30vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* border: 1px solid black; */
    border-radius: 15px;
    box-shadow: 10px 10px 20px gray;
    color: #171717;
    padding: 1em;
}

.line{
    height: 1px;
    width: 100%;
    border: 1px solid black;
}

h3{
    font-size: 38px;
}

h4{
    font-size: 28px;
}

h5{
    font-size: 20px;
    font-weight: 400;
}

.header{
    margin: 1em 0;
}

.cost{
    margin: 0.5em 0;
}

.card_pulls{
    margin: 0.5em 0;
}

.description{
    margin: 2em 0;
}

@media screen and (max-width: 720px) {
    .pricing_card_container{
        width: 90vw;
        margin: 1em 0;
        box-shadow: 5px 5px 10px gray;
    }

    h3{
        font-size: 30px;
    }
    
    h4{
        font-size: 20px;
    }
    
    h5{
        font-size: 16px;
    }
}